<template>
<div>
	<ul>
		<li v-for="(item,index) in forumLists" class="forum_Li">
			<a :href="item.Href" class="forum_li">
				<div class="game_icon"><img :src="item.imgUrl"/></div>
				<div class="forum_txt">
					<h2>{{item.title}}</h2>
					<span>主题数：{{item.theme}}</span>
				</div>
				<div class="forum_functionIcon"></div>
			</a>
		</li>
	</ul>
</div>
</template>

<script>
export default {
	name:'Forum',
	data:function() {
		return {
			forumLists:[]
		}
	},
	methods:{
		getData:function() {
			this.$http.get("./static/json/tool.json").then((response) => {
				this.forumLists = this.forumLists.concat(response.body.noneTool,response.body.tool,response.body.moreTool);
				 this.forumLists.sort(function(){ return 0.5 - Math.random() })
			})
		}
	},
	mounted:function() {
		this.getData();
  	}
}
</script>

<style>
.forum_Li .forum_li {
	display: flex;
	background: #FFFFFF;
	border-top: 1px solid #ECF1F5;
	padding: 12px 30px;
}
.forum_Li .forum_li .game_icon {
	width: 56px;
	height: 56px;
}
.forum_Li .forum_li .game_icon img {
	width: 100%;
	height: 100%;
}
.forum_Li .forum_li .forum_txt {
	margin-left: 30px;
	text-align: left;
	flex: 1;
}
.forum_Li .forum_li .forum_txt h2 {
	font-size: 20px;
	color: #000;
	margin-bottom: 8px;
}
.forum_Li .forum_li .forum_txt span {
	color: #666666;
}
.forum_Li .forum_li .forum_functionIcon {
	width: 34px;
	height: 56px;
	background: url(../assets/img/sub-btn.png) no-repeat;
	background-size: cover;
	background-position: center -109px;
}
</style>


