<template>
<header>
	<user-bar></user-bar>
	<header-main class="h-m"></header-main>
	<header-btn class="a"></header-btn>
</header>
</template>

<script>
import User from "@/components/share/header/User"
import HeaderBtn from "@/components/share/header/HeaderBtn"
import HeaderMain from "@/components/share/header/HeaderMain"
export default {
	name:'Header',
	components:{
		'user-bar':User,
		'header-main':HeaderMain,
		'header-btn':HeaderBtn,
	}
}
</script>

<style scoped>
header {
	position: fixed;
	top: 0;
	left: 0;
	height: 50px;
	width: 100%;
	z-index: 10;
	display: flex;
	/*overflow: hidden;*/
}
.h-m {
	flex: 1;
}
.a {
	position: absolute;
	top: 0;
	right: 0;
}
</style>


