<template>
<div>
	<div id="userBtn" :class="{a:($route.path==='/toolmanage')}" v-on:click="userK">
		
	</div>
	<div id="userBox"
		v-show="userKG"
		v-on:click="userK"
		>
		<div class="userB"
			v-on:click.stop>
			<router-link :to="'/'" class="userLink"
				v-for="item in user">
				<img :src="item.userImage"/>
				<div>
					<h2 class="font18 colorFFF">
						{{item.userName}}
					</h2>
					<p class="colorFFF">
						营火：{{item.userMoney}}
					</p>
				</div>
				<div class="lv colorFFF">
					Lv{{item.userLV}}
				</div>
			</router-link>
			<div>
				<ul class="userUl">
					<li v-for="(item,index) in userList">
						<router-link :to="item.Href" 
							:class="['userList'+index,'userList']">
							{{item.txt}}
						</router-link>
					</li>
				</ul>
				<ul>
					<li v-for="(item,index) in functionKey">
						<a :class="['userList'+(index+6),'userList']">
							{{item}}
						</a>			
					</li>
				</ul>
			</div>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name:'user',
	data:function() {
		return {
			userKG:false,
			user:[],
			bgImg:"",
			userList:[{
				txt:"我的播放列表",
				Href:"/",
			},{
				txt:"消息&通知",
				Href:"/",
			},{
				txt:"我的收藏",
				Href:"/",
			},{
				txt:"草稿箱",
				Href:"/",
			},{
				txt:"任务中心",
				Href:"/",
			},{
				txt:"设置",
				Href:"/",
			}],
			functionKey:["切至夜间模式","切至3/4G无图"]
		}
	},
	methods:{
		userK:function() {
			this.userKG = !this.userKG
		},
		getData:function(){
			this.$http.get('./static/json/user.json').then(response=>{
				this.user = response.body.user;
			})
		}
	},
  	mounted:function() {
		this.getData();
  	}
}
</script>
<style scoped>
#userBtn {
  height: 50px;
  width: 50px;
  background-image: url(../../../assets/img/userBtn.png);
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: center;
}
#userBox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 12;
  background-color: rgba(0, 0, 0, 0.4);
}
#userBox .userB {
  height: 100%;
  width: 50%;
  background: #ffffff;
}
.userLink {
	display: flex;
	height: 104px;
	background-image: url(../../../../static/img/hs_how2.jpg);
	padding: 20px;
	padding-right: 0;
	box-sizing: border-box;
}
.userLink img {
	width: 56px;
	height: 56px;
	border-radius: 100px;
	border: 5px solid #3B68B8;
}
.userLink div {
	flex: 1;
	margin-left: 20px;
	background: none;
}
.userLink div h2 {
	margin-bottom: 10px;
	background: none;
	overflow: hidden;
	height: 25px;
}
.userLink div p {
	background: none;
	overflow: hidden;
	height: 15px;
}
.userLink .lv {
	position: absolute;
	width: 44px;
	height: 20px;
	background: #33589b;
	text-align: center;
	line-height: 20px;
	border-radius: 6px;
	margin: 0;
	top: 75px;
	left: 30px;
}
.userUl {
	padding-bottom: 15px;
	border-bottom: 3px solid #ECF1F5;
}
.userList {
	display: block;
	font-size: 16px;
	height: 25px;
	line-height: 30px;
	padding-left: 60px;
	margin-top: 20px;
	background-position: 30px center;
	background-size: 22px 22px;
	background-repeat: no-repeat;
}
.userList0 {
	background-image: url(../../../assets/img/play.png);
}
.userList1 {
	background-image: url(../../../assets/img/comments.png);
}
.userList2 {
	background-image: url(../../../assets/img/favorite.png);
}
.userList3 {
	background-image: url(../../../assets/img/qiandao.png);
}
.userList4 {
	background-image: url(../../../assets/img/prompt.png);
}
.userList5 {
	background-image: url(../../../assets/img/set.png);
}
.userList6 {
	background-image: url(../../../assets/img/time.png);
}
.userList7 {
	background-image: url(../../../assets/img/browse.png);
}
</style>


