<template>
<div id="toolManage">
	<div id="SelectedTool">
		<div class="toolTitle">已选工具</div>
		<ul>
			<li v-for="(item,index) in selectedTool" class="tool_M_Li">
				<div class="toolM toolM_dele" v-on:click="del(index)"></div>
				<div class="toolImg">
					<img :src="item.imgUrl"/>
				</div>
				<div class="toolM_txt">
					<h2>{{item.title}}</h2>
					<span>{{item.txt}}</span>
				</div>
				<div class="toolM toolM_btn"></div>
			</li>
		</ul>
	</div>
	<div id="MoreTool">
		<div class="toolTitle">更多工具</div>
		<ul>
			<li v-for="(item,index) in moreTool" class="tool_M_Li">
				<div class="toolM toolM_add" v-on:click="add(index)"></div>
				<div class="toolImg">
					<img :src="item.imgUrl"/>
				</div>
				<div class="toolM_txt">
					<h2>{{item.title}}</h2>
					<span>{{item.txt}}</span>
				</div>
				<div class="toolM toolM_btn"></div>
			</li>
		</ul>
	</div>
</div>
</template>

<script>
export default {
	data:function() {
		return {
			selectedTool:[],
			moreTool:[]
		}
	},
	methods: {
		routerBack(){
	        this.$router.go(-1);
	    },
	    add:function(index) {
	    	var mid = this.moreTool[index];
	    	this.moreTool.splice(index,1);
	    	this.selectedTool.unshift(mid);
	    },
	    del:function(index) {
	    	var mid = this.selectedTool[index];
	    	this.selectedTool.splice(index,1);
	    	this.moreTool.unshift(mid);
	    },
	    getData:function() {
	    	this.$http.get("./static/json/tool.json").then((response)=>{
	    		this.selectedTool = response.body.tool;
	    		this.moreTool = response.body.moreTool;
	    	})
	    }
	},
	mounted:function() {
		this.getData();
  	}
}
</script>

<style type="text/css">
#toolManage {
	z-index: 10;
}
#SelectedTool {
	border-bottom: 10px solid #ECF1F5;
}
.toolTitle {
	border-top: 1px solid #ECF1F5;
	font-size: 18px;
	color: #3866BB;
	text-align: left;
	padding: 5px 8px;
}
.tool_M_Li {
	display: flex;
	border-top: 1px solid #ECF1F5;
	padding: 6px 10px;
}
.tool_M_Li .toolM {
	width: 20px;
	height: 50px;
	background: url(../../assets/img/sub-btn.png) no-repeat;
	background-size: cover;
	background-position: center 11px;
	margin-right: 10px;
	vertical-align: middle;
}
.tool_M_Li .toolM_add {
	background-position: center -23px;
}
.tool_M_Li .toolM_btn {
	background-position: center -58px;
}
.tool_M_Li .toolImg {
	width: 50px;
	height: 50px;
	vertical-align: middle;
}
.tool_M_Li .toolImg img {
	width: 100%;
	height: 100%;
}
.tool_M_Li .toolM_txt {
	text-align: left;
	margin-left: 5px;
	flex: 1;
}
.tool_M_Li .toolM_txt h2 {
	font-size: 16px;
	color: #4a4a4a;
}
.tool_M_Li .toolM_txt span {
	color: #9c9c9c;
	font-size: 12px;
	line-height: 10px;
}
</style>


