<template>
<div>
	<ul class="catalogMain">
		<li v-for="item in catalogLists">
			<a :href=item.href v-bind:class="'cata-btn'+item.num">{{item.title}}</a>
		</li>
	</ul>
</div>
</template>

<script>
export default {
	name:'Catalog',
	data:function() {
		return {
			catalogLists:[{
				title:"游戏周边",
				num:1,
				href:"javascript:void(0)"
			},{
				title:"账号充值",
				num:2,
				href:"javascript:void(0)"
			},{
				title:"极客良品",
				num:3,
				href:"javascript:void(0)"
			},{
				title:"小说书籍",
				num:4,
				href:"javascript:void(0)"
			},{
				title:"营火福利",
				num:5,
				href:"javascript:void(0)"
			},{
				title:"桌面游戏",
				num:6,
				href:"javascript:void(0)"
			},{
				title:"深夜食堂",
				num:7,
				href:"javascript:void(0)"
			},{
				title:"全部商品",
				num:8,
				href:"javascript:void(0)"
			}]
		}
	}
}
</script>

<style>
.catalogMain {
	display: flex;
	flex-wrap: wrap;
	padding: 5px 10px 5px;
	background-color: #fff;
	border-top: 1px solid #ecf1f5;
}
.catalogMain li {
	flex-basis: 25%;
}
.catalogMain li a {
	display: block;
	height: 80px;
	width: 55px;
	margin: 5px auto 0;
	line-height: 140px;
	background: url(../assets/img/foot.png) no-repeat;
	background-size: cover;
	color: #4a4a4a;
}
.catalogMain li .cata-btn1 {
	background-position: center 1px;
}
.catalogMain li .cata-btn2 {
	background-position: center -81px;
}
.catalogMain li .cata-btn3 {
	background-position: center -157px;
}
.catalogMain li .cata-btn4 {
	background-position: center -240px;
}
.catalogMain li .cata-btn5 {
	background-position: center -339px;
}
.catalogMain li .cata-btn6 {
	background-position: center -426px;
}
.catalogMain li .cata-btn7 {
	background-position: center -534px;
}
.catalogMain li .cata-btn8 {
	background-position: center -643px;
}
</style>




