<template>
<div>
	<ul>
		<li class="tool_Li" v-for="(item,index) in toolLists">
			<div class="tool_Li_title">
				<div class="game_icon"><img :src="item.imgUrl"/></div>
				<h2>{{item.title}}</h2>
				<span v-on:click="show(index)">
					<a v-if="item.on_off">收起</a>
					<a v-else>展开</a>
				</span>
			</div>
			<ul class="tool_Li_main" v-if="item.on_off">
				<li v-for="(itemChild,indexChild) in item.mainBtn">
					<a :href=itemChild.href>
						<img :src="itemChild.imgUrl"/>
						<span>{{itemChild.txt}}</span>
					</a>
				</li>
			</ul>
		</li>
	</ul>
	<!--<router-view></router-view>-->
</div>
</template>

<script>
export default {
	name:'Tool',
	data:function() {
		return {
			toolLists:[]
		}
	},
	methods: {
		show:function(index) {
			this.toolLists[index].on_off = !this.toolLists[index].on_off;
		},
		getData:function() {
			this.$http.get("./static/json/tool.json").then((response) => {
				this.toolLists = response.body.tool
			})
		}
	},
	mounted:function() {
		this.getData();
  	}
}
</script>

<style type="text/css">
.tool_Li {
	border-top: 1px solid #ECF1F5;
}
.tool_Li .tool_Li_title {
	display: flex;
	padding: 6px 10px;
	border-bottom: 1px solid #ECF1F5;
	color: #3866bb;
}
.tool_Li .tool_Li_title .game_icon img {
	width: 26px;
	height: 26px;
	border-radius: 20px;
}
.tool_Li .tool_Li_title h2 {
	flex: 1;
	text-align: left;
	font-size: 18px;
	padding-left: 10px;
}
.tool_Li .tool_Li_title span {
	font-size: 16px;
	line-height: 26px;
}
.tool_Li .tool_Li_main {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 16px;
}
.tool_Li .tool_Li_main li {
	flex-basis: 33%;
}
.tool_Li .tool_Li_main li a {
	display: block;
	text-align: center;
}
.tool_Li .tool_Li_main li a img {
	width: 80px;
	margin: 0 auto;
}
</style>


