<template>
<ul>
	<li v-for="item in List" class="indexList">
		<router-link :to="'/'">
			<div class="imgBox">
				<img :src="item.src"/>
			</div>
			<div class="ListMain">
				<h2>{{item.title}}</h2>
				<div class="List_M">
					<span v-if="$route.path=='/'">
						{{item.Class}} 
					</span>
					<span class="lei" v-if="parseInt(new Date(new Date() - item.time)/1000/60/60/24)>0">  
						{{new Date(new Date() - item.time) .getMonth()+1}}月{{new Date(new Date() - item.time).getDate()}}日
					</span>
					<span class="lei" v-else-if=" parseInt(new Date(new Date() - item.time)/1000/60/60%24)>0">   
						{{parseInt(new Date(new Date() - item.time)/1000/60/60%24)}}小时前
					</span>
					<span class="lei" v-else-if=" parseInt(new Date(new Date() - item.time)/1000/60%60)>0"> 
						{{parseInt(new Date(new Date() - item.time)/1000/60%60)}}分钟前
					</span>	
					<span class="IM_num">{{item.com_num}}</span>
				</div>
			</div>
		</router-link>
	</li>
</ul>
</template>

<script>
export default {
	name:'IndexMain',
	data:function() {
		return {
			List:[]
		}
	},
	methods: {
		getData:function() {
			var Json = "";
			switch(this.$route.path) {
					case '/index/news': Json = './static/json/news.json';
						break;
					case '/index/hs': Json = './static/json/hs_how.json';
						break;
					case '/index/dl': Json = './static/json/DL_link.json';
						break;
					case '/index/ow': Json = './static/json/ow.json';
						break;
			};
			this.$http.get(Json).then((response) => {
				this.List = response.body.bannerlist;
			})
		}
	},
  	mounted:function() {
		this.getData();
  	}
}
</script>

<style>
.indexList {
	
}
.indexList a {
	display: flex;
	padding: 8px 15px;
}
.imgBox {
	width: 125px;
	height: 75px;
	margin-right: 12px;
}
.imgBox img {
	width: 100%;
	height: 100%;
}
.ListMain {
	position: relative;
	flex: 1;
}
.ListMain h2 {
	font-size: 18px;
	height: 54px;
	text-overflow: ellipsis;
}
.List_M {
	position: absolute;
	bottom: 0;
	display: flex;
	width: 100%;
}
.List_M .lei {
	flex: 1;
	display: block;
}
.List_M .IM_num {
	padding-left: 20px;
	background-image: url(../../assets/img/show-comment.png);
	background-size: contain;
	background-repeat: no-repeat;
}
</style>


