<template>
<swiper :options="swiperOption">
	<swiper-slide v-for="slide in swiperSlides"
		 v-bind:style="{height:wid+'px'}">
		<img :src="slide.src"/>
		<div class="swiperTxt">{{slide.title}}</div>
	</swiper-slide>
	
	<div class="swiper-pagination" slot="pagination" :style="{bottom: 0}"></div>
</swiper>
</template>

<script>
import {swiper,swiperSlide,swiperPlugins} from 'vue-awesome-swiper'
export default {
	components:{swiper,swiperSlide},
	data(){
		return {
			wid: window.innerWidth*0.618,
			swiperOption:{
				autoplay:3000,
				mousewheelControl : true,
				loop:true,
				autoplayDisableOnInteraction:false,
				pagination : '.swiper-pagination',
          		paginationClickable :true,
			},
			swiperSlides:[]
		}
	},
	methods: {
		getData:function() {
			var Json = "";
			switch(this.$route.path) {
					case '/index/news': Json = './static/json/news.json';
						break;
					case '/index/hs': Json = './static/json/hs_how.json';
						break;
					case '/index/dl': Json = './static/json/DL_link.json';
						break;
					case '/index/ow': Json = './static/json/ow.json';
						break;
			};
			this.$http.get(Json).then((response) => {
				this.swiperSlides = response.body.bannerlist;
				this.swiperSlides = this.swiperSlides.slice(0,3)
			})
		}
	},
  	mounted:function() {
		this.getData();
  	}
}
</script>

<style scoped>
.swiper-pagination {
	background: rgba(0,0,0,0);
}
.swiperTxt {
	position: absolute;
	left: 0;
	bottom: 0px;
	background: rgba(0,0,0,0.4);
	width: 100%;
	color: #fff;
	height: 30px;
}
</style>


