<template>
<div id="hunt">
	<header id="hunt-header">
		<input type="text" name="" id="" v-model="todo" v-on:keyup.enter="add" placeholder="输入标题或关键字"/><a href="javascript:void(0)"  @click="routerBack"></a>
	</header>
	<div id="hunt-main">
		<div class="hun-B">
			<a href="javasrcipt:void(0)" 				v-for="(item,index) in hunBtn"
				v-bind:class="['hun-btn','hun-btn'+(index+1),{active:index === activeIndex}]"  
				v-on:click="tab(index)">{{item}}</a>
		</div>
		<div class="hun-M">
			<div class="HM2" v-if="activeIndex">
				<ul class="hun-sou2">
					<li v-for="(item,index) in souList2">
						{{item}}<span v-on:click="del(index)"></span>
					</li>
				</ul>
				<a href="javascript:void(0)" class="hun-delete" v-on:click="Del">清除历史记录</a>
			</div>
			<div class="HM1" v-else>
				<ul class="hun-sou1">
					<li v-for="(item,index) in souList1">
						{{item}}
					</li>
				</ul>
			</div>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name:'hunt',
	data:function() {
		return {
			todo:"",
			activeIndex:0,
			hunBtn:["推荐热搜","历史记录"],
			souList1:["众选竞技场","深夜营地","守望漫画","宝可梦GO","营地啥都聊","外域之声","逗鱼时刻","美漫联盟","环境周报","桌游文化","新游尝鲜","卡组日报"],
			souList2:["深夜营地","天梯","死鱼骑","决斗链接"]
		}
	},
	methods: {
		tab: function(index) {
			this.activeIndex = index;
		},
		del:function(index) {
			this.souList2.splice(index,1);
		},
		Del:function() {
			this.souList2=[];
		},
		add:function() {
			this.souList2.unshift(this.todo.trim());
			this.todo="";
		},
		routerBack(){
	        this.$router.go(-1);
	    }
	}
}
</script>

<style>
#hunt {
	width: 100%;
	height: 100%;
	background-color: #fff;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;
	text-align: center;
}
#hunt-header {
	display: flex;
	width: 90vw;
	margin: 10px auto;
}
#hunt-header input {
	background: url(../assets/img/header.png) no-repeat;
	background-position: 0 -146px;
	background-size: 30px;
	padding-left: 34px;
	height: 32px;
	border: none;
	background-color: #ecf1f5 ;
	border-radius: 10px;
	margin-right: 10px;
	flex: 1;
	color: #3b68b8;
}
#hunt-header input::-webkit-input-placeholder {
	color: #3b68b8;
}
#hunt-header a {
	width: 32px;
	height: 32px;
	display: block;
	background-image: url(../assets/img/escBtn.png) ;
	background-repeat: no-repeat;
	background-size: cover;
	margin-right: -10px;
} 

#hunt-main {
	
}
#hunt-main .hun-B {
	border-bottom: 1px solid #ecf1f5;
	display: flex;
}
#hunt-main .hun-B .hun-btn {
	display: inline-block;
	flex: 1;
	color: #9b9b9b;
	padding-bottom: 5px;
}
#hunt-main .hun-B .active {
	color: #3b68b8;
}
#hunt-main .hun-M .hun-sou1 {
	display: flex;
	/*display: none;*/
	flex-wrap: wrap;
	padding: 4px 5vw;
	justify-content: space-around;
}
#hunt-main .hun-M .hun-sou1 li {
	flex-basis: 20%;
	width: 80px;
	height: 36px;
	margin: 2px 2px;
	line-height: 36px;
	background-color: #ecf1f5;
	border-radius: 8px;
}
#hunt-main .hun-M .hun-sou2 {
	/*display: flex;*/
	/*flex-wrap: wrap;*/
	padding: 5px 0;
}
#hunt-main .hun-M .hun-sou2 li {
	/*flex: 1;*/
	text-align: left;
	padding-left: 18px;
	border-bottom: 1px solid #ECF1F5;
	height: 28px;
	line-height: 28px;
}
#hunt-main .hun-M .hun-sou2 li span {
	content: "";
	display: block;
	height: 28px;
	width: 28px;
	float: right;
	margin-right: 15px;
	background-image: url(../assets/img/escBtn.png) ;
	background-repeat: no-repeat;
	background-size: 18px 18px;
	background-position: center;
}
#hunt-main .hun-M 	 .hun-delete {
	display: inline-block;
	padding: 8px 12px;
	background-color: #ecf1f5;
	border-radius: 8px;
}
</style>


