<template>
<div>
	<div id="mysub">
		<div class="subTitle">
			<h2>我的订阅</h2>
			<span v-on:click="paiXu">
				<span v-show="paixu">排序</span>
				<span v-show="!paixu">完成</span>
			</span>
		</div>
		<ul>
			<li v-for="(item,index) in gameList" class="SubList">
				<router-link :to=item.Href  class="subList">
					<img :src="item.imgUrl"/>
					<div class="subList_main">
						<div class="subList_top">
							<h3>{{item.title}}</h3>
							<span>今日更新：{{item.updataNum}}</span>
						</div>
						<span>{{item.txt}}</span>
					</div>
				</router-link>
				<div class="subBtn subBtn_1" v-show="paixu" v-on:click.stop="del(index)"></div>
					<div class="subBtn subBtn_2" v-show="!paixu"></div>
			</li>
		</ul>
	</div>
	<div>
		<div class="subTitle">
			<h2>更多订阅</h2>
		</div>
		<ul>
			<li v-for="(item,index) in moreList" class="SubList">
				<router-link :to="item.Href"  class="subList">
					<img :src="item.imgUrl"/>
					<div class="subList_main">
						<div class="subList_top">
							<h3>{{item.title}}</h3>
							<span>今日更新：{{item.updataNum}}</span>
						</div>
						<span>{{item.txt}}</span>
					</div>
				</router-link>
				<div class="subBtn subBtn_3" v-show="paixu" v-on:click.stop="add(index)"></div>
			</li>
		</ul>
	</div>
</div>
</template>

<script>
export default {
	name:'Index',
	data:function() {
		return {
			gameList:[],
			moreList:[],
			paixu:true
		}
	},
	methods:{
		getData:function() {
			this.$http.get("./static/json/list.json").then((response) => {
				this.gameList = response.body.list;
				this.moreList = response.body.morelist;
			})
		},
		paiXu:function() {
			this.paixu = !this.paixu;
		},
		add:function(index) {
			var mid = this.moreList[index];
	    	this.moreList.splice(index,1);
	    	this.gameList.unshift(mid);
		},
	    del:function(index) {
	    	if(this.gameList.length<=2) {
	    		alert("订阅不能少于两个");
	    		return;
	    	}
	    	var mid = this.gameList[index];
	    	this.gameList.splice(index,1);
	    	this.moreList.unshift(mid);
	    }
	},
	mounted:function() {
		this.getData();
	}
}
</script>

<style>
#mysub {
	border-bottom: 10px solid #ECF1F5;
}
.subTitle {
	display: flex;
	padding: 10px;
	border-top: 1px solid #ECF1F5;
	padding-right: 18px;
}
.subTitle h2 {
	flex: 1;
	text-align: left;
	font-size: 16px;
}
.subTitle span {
	font-size: 14px;
	line-height: 24px;
}
.SubList {
	display: flex;
	border-top: 1px solid #ECF1F5;
	padding: 12px 16px;
}
.subList {
	flex: 1;
	line-height: 16px;
	display: flex;
}
.subList img {
	display: block;
	width: 50px;
	height: 50px;
	margin-right: 12px;
}
.subList .subList_main {
	flex: 1;
	color: #9B9B9B;
	text-align: left;
}
.subList .subList_main .subList_top {
	display: flex;
	margin: 4px 0 12px;
}
.subList .subList_main .subList_top h3 {
	font-size: 16px;
	margin-right: 8px;
	color: #4A4A4A;
}
.subList .subBtn ,.subBtn{
	width: 30px;
	height: 50px;
	background: url(../../assets/img/sub-btn.png) no-repeat;
	background-size: cover;
}
.subList .subBtn_1 ,.subBtn_1{
	background-position: center 9px;
}
.subList .subBtn_2 ,.subBtn_2{
	background-position: center -97px;
}
.subList .subBtn_3 ,.subBtn_3{
	background-position: center -47px;
}
</style>


