<template>
<div class="headerMain">
	<div class="indexMain" v-show="!($route.path.indexOf('/index')=== -1)">
		<ul v-bind:style="{width:ulWidth}">
			<li v-for="(item,index) in headerList">
				<router-link :to="item.Href"
					:class="{active:($route.path === item.Href)}">
					{{item.title}}
				</router-link>
			</li>
		</ul>
	</div>
	<div v-show="$route.path==='/tool'" class="headerMain">
		我的工具
	</div>
	<div v-show="$route.path==='/forum'" class="headerMain">
		论坛列表
	</div>
	<div v-show="$route.path==='/shop'" class="headerMain">
		商城
	</div>
	<div v-show="$route.path==='/toolmanage'" class="headerMain">
		工具管理
	</div>
</div>
</template>

<script>
export default {
	data:function() {
		return {
			headerList:[{
				Href:'/index/sub',
				title:'订阅'
			},{
				Href:'/index/news',
				title:'资讯'
			}],
			ulWidth:0
		}
	},
	methods:{
		getData:function() {
			this.$http.get("./static/json/list.json").then((response) => {
	   			this.headerList = this.headerList.concat(response.body.list);
	   		}).then(function() {
	   			this.ulWidth = 65 * this.headerList.length + 'px';
	   		})
		}
	},
	mounted:function() {
		this.getData();
	}
}
</script>

<style scoped>
.headerMain {
	text-align: center;
	margin-right: 50px;
	overflow: hidden;
	overflow-x: scroll;
	height: 50px;	
}
.indexMain {
	height: 100%;
}
.indexMain ul {
	display: flex;
	height: 65px;
}
.indexMain ul li {
	font-size: 16px;
	line-height: 50px;
}
.indexMain ul li a {
	display: block;
	line-height: 50px;
	margin: 0 10px;
}
.indexMain ul li .active {
	font-size: 24px;
}
.headerMain {
	text-align: center;
	width: 100%;
	font-size: 20px;
	line-height: 50px;
}
</style>


