import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Tool from '@/components/Tool'
import Forum from '@/components/Forum'
import Shop from '@/components/Shop'
import Sub from '@/components/index/Sub'
import Hs from '@/components/index/Hs'
import DL from '@/components/index/DL'
import Ow from '@/components/index/Ow'
import News from '@/components/index/News'
import ToolManage from '@/components/tool/ToolManage'
import Hunt from '@/components/Hunt'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index/',
      name: 'Index',
      component: Index,
      children:[{
      	path:'news',
      	component:News
      },{
      	path:'sub',
      	component:Sub
      },{
      	path:'hs',
      	component:Hs
      },{
      	path:'dl',
      	component:DL
      },{
      	path:'ow',
      	component:Ow
      }]
    },{
    	path: '/tool',
      name: 'Tool',
      component: Tool
    },{
    	path: '/forum',
      name: 'Forum',
      component: Forum
    },{
    	path: '/shop',
      name: 'Shop',
      component: Shop
    },{
    	path: '/toolmanage',
    	component: ToolManage
    },{
      path: '/hunt',
      name: 'Hunt',
      component: Hunt
    }
  ]
})



