<template>
<div>
	<ul>
		<li v-for="(item,index) in shopLists" class="shop_li">
			<div class="shop_title">
				<h2>{{item.title}}</h2><a :href=item.href>查看更多</a>
			</div>
			<div class="shop_main">
				<a v-for="(itemChild,Index) in item.main" :href=itemChild.Href>
					<img :src="itemChild.imgUrl"/>
					<span>{{itemChild.txt}}</span>
				</a>
			</div>
		</li>
	</ul>
</div>
</template>

<script>
export default {
	name:'ShopMain',
	data:function() {
		return {
			shopLists:[]
		}
	},
	methods:{
		getData:function() {
			this.$http.get("./static/json/shop.json").then(response => {
				this.shopLists = response.body.shopLists;
			})
		}
	},
	mounted:function() {
		this.getData();
  	}
}
</script>

<style>
.shop_li {
	border-top: 10px solid #ECF1F5;
	background-color: #fff;
}
.shop_li .shop_title {
	display: flex;
	padding: 10px 5px 0;
	color: #4a4a4a;
	border-bottom: 1px solid #ecf1f5;
}
.shop_li .shop_title h2 {
	display: inline;
	flex: 1;
	text-align: left;
	font-size: 16px;
	vertical-align: middle;
}
.shop_li .shop_title a {
	color: #4a4a4a;
	line-height: 24px;
	vertical-align: middle;
}
.shop_li .shop_main {
	display: flex;
}
.shop_li .shop_main a {
	display: block;
	flex: 1;
	padding: 4px;
	border-left: 1px solid #ECF1F5;
	text-align: left;
}
.shop_li .shop_main a img {
	border-radius: 8px;
	height: 80px;
	/*width: 100px;*/
	margin-bottom: 4px;
}
.shop_li .shop_main a span {
	font-size: 14px;
	color: #4a4a4a;
}
</style>


