<template>
<footer>
<ul>
	<li v-for="(item,index) in footerList"
		v-bind:class="['foot-icon-'+(index+1),{active:($route.path === item.path)}]">
		<router-link :to="item.path" class="font12">
			{{item.title}}
		</router-link>
	</li>
</ul>
</footer>
</template>

<script>
export default {
	name:'footer',
	data:function() {
		return {
			footerList:[{
				path:'/index/news',
				title:'资讯'
			},{
				path:'/tool',
				title:'工具'
			},{
				path:'/forum',
				title:'论坛'
			},{
				path:'/shop',
				title:"商城"
			}]
		}
	}
}
</script>

<style scoped>
footer {
  border-top: 1px solid #ecf1f5;
  position: fixed;
  bottom: 0;
  left: 0;
  background: #ffffff;
  width: 100%;
  height: 54px;
}
footer ul {
  display: flex;
  height: 100%;
}
footer ul li {
  flex: 1;
  height: 100%;
  text-align: center;
}
footer ul li a {
	display: block;
	height: 100%;
	line-height: 84px;
	background-size: 20px 20px;
  	background-position: center 10px;
  	background-repeat: no-repeat;
}

footer ul .active a{
	color: #3a3a3a;
}
footer ul .foot-icon-1 a {
	background-image: url(../../assets/img/home.png);
}
footer ul .foot-icon-1.active a {
	background-image: url(../../assets/img/home_a.png); 
}
footer ul .foot-icon-2 a {
	background-image: url(../../assets/img/tool.png);
}
footer ul .foot-icon-2.active a {
	background-image: url(../../assets/img/tool_a.png); 
}
footer ul .foot-icon-3 a {
	background-image: url(../../assets/img/forum.png);
}
footer ul .foot-icon-3.active a {
	background-image: url(../../assets/img/forum_a.png); 
}
footer ul .foot-icon-4 a {
	background-image: url(../../assets/img/shop.png);
}
footer ul .foot-icon-4.active a {
	background-image: url(../../assets/img/shop_a.png); 
}
</style>


